import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { IonItem, ModalController } from '@ionic/angular';
import { DialogService } from 'src/app/api/util/dialog.service';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { UtilService } from 'src/app/services/util.service';
import { FieldFormPage } from '../field-form/field-form.page';
import { ModalForm } from 'src/app/models/modal-form';
import { PlatformLocation } from '@angular/common';
import { AuthUserService } from 'src/app/services/auth-user.service';

import * as Leaflet from 'leaflet';
import { ShowPage } from '../show/show.page';
import { NavParams } from 'node_modules_3/@ionic/angular';
import { TransferDataService } from 'src/app/services/transfer-data.service';


@Component({
  selector: 'app-list-fields',
  templateUrl: './list-fields.page.html',
  styleUrls: ['./list-fields.page.scss'],
})
export class ListFieldsPage extends ModalForm implements OnInit , ModalForm {


  segment_select = 'list';
  fields = [];
  oldFields = [];
  markers = [];

  modelField : Model;
  public canEdit = false;

  map: Leaflet.Map;

  isSelect = false;


  constructor(
    public requestService : RequestService,
    public dialogService : DialogService,
    public modalController : ModalController,
    public utilArray : UtilArrayService,
    public util : UtilService,
    location: PlatformLocation,
    public viewCtrl: ModalController,
    public dialog : DialogService,
    public authUser : AuthUserService,
    public transfDat : TransferDataService
  
  ) {
    super(location,viewCtrl)
    this.modelField =  new Model('Field',requestService);

    this.isSelect = transfDat.getData('isSelect');
    transfDat.clear();

    //this.isSelect = navParams.get('isSelect') ;

   }

  ngOnInit() {

    //this.util.setScript('https://maps.googleapis.com/maps/api/js?key=AIzaSyA_uVKWprjIgPURNhl1v9zzTPLQJIBdi6I');
    this.init();

  }

  async init(){
    await this.dialogService.loadingProgres();

    this.modelField.api_function('pageAllFields').subscribe(
      response => {
        if(response['status'] == 'success'){
          this.fields = response['data'];
          this.oldFields = this.fields;
        }

        this.leafletMap();
        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging();
        this.dialogService.showToast('Error al obtener Campos','','warning');
      }
    )
  }


  async add() {
    const modal = await this.modalController.create({
      component: FieldFormPage ,
    });

    modal.onDidDismiss().then(data=>{
      if(data['data'] && data.data['field']){
        const field = data.data['field'];
        this.utilArray.listAddFirst(this.fields,field);
      }
    })

    return await modal.present();
   }

   filterField(event){
     

    let text = event.target.value;
    if(text.length <= 0){
      this.fields = this.oldFields;
      this.loadFields();
      return;
    }

    this.fields = [... this.oldFields];

    this.fields = this.fields.filter(field => field.name.toUpperCase().includes(text.toUpperCase()));

    this.loadFields();

  }

  cancelFilter($event){
    this.fields = this.oldFields;
  }

  dismiss(field) {
    this.viewCtrl.dismiss(
      {
        field : field,
      }
    );
  }

  close(){
    this.viewCtrl.dismiss();
  }

  confirmDelete(field,$event){
    $event.stopPropagation();

    this.dialog.presentAlertConfirm('Confirmar','Estas seguro de eliminar esta cancha',e => {
      this.delete(field)
    });
   }

   delete(field){
     this.modelField.api_delete(field.id).subscribe(data => {
       console.log(data);
       if(data['status'] == 'success'){
         this.dialog.showToast('Cancha Eliminada',null,'success');
         this.utilArray.listDelete(this.fields,field.id)
       }
     });
   }

   async showEdit(field,$event){
     console.log('showEdit')
    $event.stopPropagation();

    const modal = await this.modalController.create({
      component: FieldFormPage ,
      componentProps: { id: field.id }
    });

    modal.onDidDismiss().then(data=>{
      if(data.data && data.data['field']){
        const field = data.data['field'];
        this.utilArray.listUpdate(this.fields,field.id,field)
      }
    })

    return await modal.present();
   }

  async show(field,$event){
    if(this.isSelect)
      return;

    console.log('show')
   $event.stopPropagation();

   const modal = await this.modalController.create({
     component: ShowPage ,
     componentProps: { id: field.id }
   });

   modal.onDidDismiss().then(data=>{
     if(data.data && data.data['field']){
       //const field = data.data['field'];
       //this.utilArray.listUpdate(this.fields,field.id,field)
     }
   })

   return await modal.present();
  }

   segmentChanged(event){
    //console.log(event);
    this.segment_select = event.detail.value;
    setTimeout(()=>{
      this.regresh();

    },100)
   }


   
  leafletMap() {

    //this.reset();
    let field = { 
      name : '',
      lat : -27.603857971720405,
      lng : -55.323644024643414
    };  

    this.map = Leaflet.map('mapId3');
    
    this.map.on("load",() => { 
      console.log('load map')

      setTimeout(() => {
        console.log('refresh')
      this.regresh();
    }, 1000); });
    
    
    this.map.setView([field.lat,field.lng], 11);

    //old
    Leaflet.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'futbol8alem.com',
      id: 'mapbox/streets-v11'
    }).addTo(this.map);



   
/*
    Leaflet.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'futbol8alem.com',
    }).addTo(this.map);
*/
    //Leaflet.marker([28.6, 77]).addTo(this.map).bindPopup('Delhi').openPopup();
    //Leaflet.marker([34, 77]).addTo(this.map).bindPopup('Leh').openPopup();

    

    this.loadFields();
  }


  loadFields(){

    this.markers.forEach(el => {
      if(this.map)
        this.map.removeLayer(el);
    })

    this.markers = [];

  
    this.fields.forEach(element => {
      this.addMarker( parseFloat(element.lat),parseFloat( element.lng) ,element.name);
    });

    this.regresh();
  }

  addMarker(lat,lng,title){

    var Icono = Leaflet.icon({
      iconUrl: "http://icons.iconseeker.com/png/fullsize/sport-fields/football-field.png",
      iconSize: [30, 40],
      iconAnchor: [15, 40],
      shadowSize: [35, 50],
      shadowAnchor: [0, 55],
      popupAnchor: [0, -40]});

    if(lat != 0 && lng != 0){
      var m = Leaflet.marker([lat, lng],{icon:Icono}).addTo(this.map).bindTooltip(title,{permanent : true}).openTooltip()
      //.bindPopup(title,{autoPan:false}).openPopup();
      this.markers.push(m);
    }
  }

  reset(){
    if(this.map){
      this.map.off();
      this.map.remove();
    }
  }

  regresh(){
    this.map.invalidateSize();
  }

}
