import { Component, OnInit } from '@angular/core';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalController, NavParams } from '@ionic/angular';
import { PhotoService } from 'src/app/services/photo.service';
import { Model } from 'src/app/api/models/model';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { PlatformLocation } from '@angular/common';

import * as Leaflet from 'leaflet';
import { antPath } from 'leaflet-ant-path';


@Component({
  selector: 'app-field-form',
  templateUrl: './field-form.page.html',
  styleUrls: ['./field-form.page.scss'],
})
export class FieldFormPage extends ModalForm implements OnInit , ModalFormInterface {

  public modelField : Model;
  public id: any;
  public field ;

  map: Leaflet.Map;
  newMarker = null;

  position : any;


  constructor(
    public request : RequestService,
    public dialogService : DialogService,
    public viewCtrl: ModalController,
    public photoService : PhotoService,
    navParams: NavParams,
    location: PlatformLocation,


  ) {
    super(location,viewCtrl)

    this.modelField = new Model('Field',request);

    this.id = navParams.get('id') ;

    

    
   }


   initObject(){
    
    console.log('initObject');

    this.field = { 
      name : '',
      lat : -27.603857971720405,
      lng : -55.323644024643414
    };  

    if(navigator.geolocation){
      navigator.geolocation.getCurrentPosition((position) => {
           this.field.lat= position.coords.latitude;
          this.field.lng = position.coords.longitude;
        console.log('current pos',position)
      },error =>{
        console.error(error)
      })
    }

    if(this.id){
      this.modelField.api_find(this.id).subscribe(data => {
        console.log('load field', data)
        if(data['status'] == 'success'){
          this.field = data['Field'];
          this.leafletMap();
          this.addNewMarker();
        }
      })
    }else{
      this.leafletMap();
    }


  }

  submit(){
    if(this.field.id){
      this.modelField.api_update(this.field).subscribe(data => {
        if(data['status'] == 'success'){
          this.dialogService.showToast( data['msjSuccess'] ?? 'Registrado' );
          this.dismiss(data['Field'])
        }
      });

    }else{
      this.modelField.api_create(this.field).subscribe(data => {
        if(data['status'] == 'success'){
          this.dialogService.showToast( data['msjSuccess'] ?? 'Registrado' );
          this.dismiss(data['Field'])
        }
    })
    }
  }

  dismiss(field) {
    this.viewCtrl.dismiss(
      {
        field : field,
      }
    );
  }

  close(){
    this.viewCtrl.dismiss();
  }

  leafletMap() {

    this.reset();

    this.map = Leaflet.map('mapId2');

    const self = this;
    
    this.map.on("load",() => { 
      console.log('load map')

      setTimeout(() => {
        console.log('refresh')
      this.regresh();
    }, 1000); });
    
    
    this.map.setView([this.field.lat,this.field.lng], 11);

    //old
    Leaflet.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'futbol8alem.com',
      id: 'mapbox/streets-v11'
    }).addTo(this.map);


   

    this.map.on('contextmenu',e => {
      console.log('rightClick' , e)

      this.field.lat = e.latlng.lat;
      this.field.lng = e.latlng.lng;

      this.addNewMarker();

    });


   
/*
    Leaflet.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'futbol8alem.com',
    }).addTo(this.map);
*/
    //Leaflet.marker([28.6, 77]).addTo(this.map).bindPopup('Delhi').openPopup();
    //Leaflet.marker([34, 77]).addTo(this.map).bindPopup('Leh').openPopup();

    

    this.loadFields();
  }

  addNewMarker(){

    if(this.newMarker)
        this.map.removeLayer(this.newMarker);
      
    const myCustomColour = '#35da7f'

    const markerHtmlStyles = `
      background-color: ${myCustomColour};
      width: 2rem;
      height: 2rem;
      display: block;
      left: -1.5rem;
      top: -1.5rem;
      position: relative;
      border-radius: 3rem 3rem 0;
      transform: rotate(45deg);
      border: 1px solid #FFFFFF`

    const icon = Leaflet.divIcon({
      className: "my-custom-pin",
      iconAnchor: [0, 24],
      labelAnchor: [-6, 0],
      popupAnchor: [0, -36],
      html: `<span style="${markerHtmlStyles}" />`
    })


    this.newMarker = Leaflet.marker([this.field.lat , this.field.lng],{
      draggable : true,
      alt : 'Nueva Cancha',
      shadowPane : 'Pane',
      icon : icon,
      title : 'Arrastra bara canbiar la ubicación.'
      })
    .addTo(this.map);

    this.newMarker.on('dragend', (event) => {
      var marker = event.target;
      var position = marker.getLatLng();

      this.field.lat = position.lat;
      this.field.lng = position.lng;

      marker.openPopup();
      console.log('change pos' , this.field)

      
  });

    this.newMarker.bindPopup(this.field.name,{autoPan:false})
                  .openPopup();
  }


  async loadFields(){

    if(! this.id)
      this.modelField.api_function('markers').subscribe(
        response => {
          if(response['status'] == 'success'){
            if(response['data']){
              response['data'].forEach(element => {
                this.addMarker( parseFloat(element.lat),parseFloat( element.lng) ,element.name);
              });
            }
          }
          this.map.setView([this.field.lat,this.field.lng], 11);  
        },
        error => {

        }
      )
  }

  addMarker(lat,lng,title){

    var Icono = Leaflet.icon({
      iconUrl: "http://icons.iconseeker.com/png/fullsize/sport-fields/football-field.png",
      iconSize: [30, 40],
      iconAnchor: [15, 40],
      shadowSize: [35, 50],
      shadowAnchor: [0, 55],
      popupAnchor: [0, -40]});

    if(lat != 0 && lng != 0)
      Leaflet.marker([lat, lng],{icon:Icono}).addTo(this.map).bindTooltip(title,{permanent : true}).openTooltip()
      //.bindPopup(title,{autoPan:false}).openPopup();

  }


  inputVal(val){
    this.field.name = val;

    if(this.newMarker){
      this.newMarker._popup.setContent(val,{autoPan:false})

    }
  }

  regresh(){
    this.map.invalidateSize();
  }

  reset(){
 
    
    if(this.map){
      this.map.off();
      this.map.remove();
    }
  }
  

}
